
.page-header {
	@extend .cf;
	background: #fff;
	z-index: 999;
	padding: 20px 0;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	min-height: $menu-height;
	box-shadow: 0px 1px 1px 0px rgba(0,0,0,0.2);

	.container {
		padding: 0 20px;
	}

	&.menu-visible {
		.page-nav {
			display: block;
		}
	}
}
