
$gutterwidth: 3;

.columns {
	overflow: hidden;

	&:not(:last-child) {
		margin-bottom: 30px;

		@include bp(lap) {
			margin-bottom: 50px;
		}
	}

	@include bp(lap) {

		&.-two-col {
			> ._column {
				@include col($ratios: 1/2, $cycle: 2, $gutter: $gutterwidth);
			}
		}

		&.-three-col {
			> ._column {
				@include col($ratios: 1/3, $cycle: 3, $gutter: $gutterwidth);
			}
		}

		&.-four-col {
			> ._column {
				@include col($ratios: 1/4, $cycle: 4, $gutter: $gutterwidth);
			}
		}


		&.-two-span {
			> ._column {
				@include span($ratio: 1/2, $cycle: 2);
			}
		}

		&.-three-span {
			> ._column {
				@include span($ratio: 1/3, $cycle: 3);
			}
		}

		&.-four-span {
			> ._column {
				@include span($ratio: 1/4, $cycle: 4);
			}
		}


		._column {
			&.-one-half-col {
				@include col($ratios: 1/2, $gutter: $gutterwidth);
			}
			&.-one-third-col {
				@include col($ratios: 1/3, $gutter: $gutterwidth);
			}
			&.-two-thirds-col {
				@include col($ratios: 2/3, $gutter: $gutterwidth);
			}
			&.-one-quarter-col {
				@include col($ratios: 1/4, $gutter: $gutterwidth);
			}
			&.-two-quarters-col {
				@include col($ratios: 2/4, $gutter: $gutterwidth);
			}
			&.-three-quarters-col {
				@include col($ratios: 3/4, $gutter: $gutterwidth);
			}

			&.-one-half-span {
				@include span($ratio: 1/2);
			}
			&.-one-third-span {
				@include span($ratio: 1/3);
			}
			&.-two-thirds-span {
				@include span($ratio: 2/3);
			}
			&.-one-quarter-span {
				@include span($ratio: 1/4);
			}
			&.-two-quarters-span {
				@include span($ratio: 2/4);
			}
			&.-three-quarters-span {
				@include span($ratio: 3/4);
			}
		}

	}

	> ._row {
		@extend .cf;
		margin-bottom: 30px;
	}
}
