
.page-hero {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: top center;
	color: white;
	text-align: center;
	padding: 20px;
	position: relative;

	@include bp(lap) {
		padding: 60px 0;
	}

	&:before {
		content: "";
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		background: rgba(0,0,0,0.35);
		z-index: 0;
	}

	.container {
		position: relative;
		z-index: 1;
	}
}
